import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/blog',
      name: '网络日志',
      component: () => import(/* webpackChunkName: "List" */ './List.vue')
    },
    {
      path: '/blog/:year/:month/:day/:title',
      name: '日志',
      props:true,
      component: () => import(/* webpackChunkName: "Content" */ './Content.vue')
    },
    { path: '*', redirect: '/blog' }
  ]
})
